const todoForm = document.querySelector(".todo_form");
const todoInput = todoForm.querySelector("input");
const todoList = document.querySelector(".todo_List");

const TODOS_LS = "toDos"; //localStorageì˜ key:value

let toDos = [];

//HTML ì‚­ì œ
function deleteToDo(e) {
	const btn = e.target;

	//console.dir(btn); -> ë¶€ëª¨ìš”ì†Œì¸ lië¥¼ ì°¾ìŒ
	const li = btn.parentNode;
	todoList.removeChild(li);

	//localStorageì—ì„œ ì‚­ì œ
	//todo.idì™€ li.idì˜ typeì´ ë‹¤ë¥´ë‹¤. parseInt();
	const cleanToDos = toDos.filter((todo) => todo.id !== parseInt(li.id));

	//ì´ì „ toDosë¥¼ ë³€ê²½ëœ cleanToDosë¡œ update
	//toDosì˜ ë³€ìˆ˜ ì„¤ì •ì´ const -> letìœ¼ë¡œ ë³€ê²½
	toDos = cleanToDos;

	//ìƒˆë¡œìš´ ë°°ì—´ì„ ì €ìž¥í•¨
	saveToDos()
}

function saveToDos() {
	localStorage.setItem(TODOS_LS, JSON.stringify(toDos));
}
//localStorageì—ëŠ” js dataë¥¼ ì €ìž¥ í•  ìˆ˜ ì—†ë‹¤. ì˜¤ì§typeì´ stringë§Œê°€ëŠ¥

const paintToDo = (text) => {
const li = document.createElement("li");
	const delBtn = document.createElement("button");
	delBtn.innerText = "âŒ";
	delBtn.addEventListener("click", deleteToDo);
	const span = document.createElement("span");
	const newId = toDos.length + 1;
	span.innerText = text

	li.appendChild(delBtn);
	li.appendChild(span);
	li.id = newId;
	todoList.appendChild(li);

	const toDoObj = {
		id: newId,
		text: text,
	};

	toDos.push(toDoObj);
	saveToDos();
};

function handleSubmit(e) {
	e.preventDefault();
	const currentValue = todoInput.value;
	paintToDo(currentValue);
	todoInput.value = "";
}

function loadTodos() {
	const loadToDos = localStorage.getItem(TODOS_LS);
	if (loadToDos !== null) {
		const parsedToDos = JSON.parse(loadToDos);
		parsedToDos.forEach((todo) => paintToDo(todo.text));
	}
}

function init() {
	loadTodos();
	todoForm.addEventListener("submit", handleSubmit);
}
init();
